import React from 'react';
import "./contacts.css";
import { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';

function Contacts() {
  const formRef = useRef(); 
  const [done, setDone] = useState(false)

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_1co2o3a', 'template_0rk077t', formRef.current, {
        publicKey: 'Aa3pIsxRMXnDxkZz9',
      })
      .then(
        (result) => {
          console.log('SUCCESS!', result.text);
          setDone(true);
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  }

  return (
    <div>
      <div className='contact-container'>
        <h4 className='contact-title'>Contact Us</h4>
        <div className='contact-content'>
          <div className='contacts'>
            <h4 className='contacts-title'>Contacts</h4>
            <h5>Email: paccymaker@gmail.com</h5>
            <h5>Tel: +250780670284</h5>
            <h4>I have been working in the field of software development for 3 years and have a strong background in Front-end.
              <br />
              I am particularly passionate about Front-end and Backend (A Full-stack), and I am always eager to stay updated on the latest industry trends and technologies.
            </h4>
            <button className='hire'>Hire Me</button>
          </div>

          <div className='contact-form'>
            <h5 className='form-title'>Leave your Opinion</h5>
            <form ref={formRef} onSubmit={handleSubmit}>
              <input type='text' name='user_name' placeholder='Name' required />
              <input type='email' name='user_email' placeholder='Email' required />
              <textarea name='message' rows="8" placeholder='Write message...' required></textarea>
              <br /><button type='submit' name='submit'>Send</button> {done && "Thank your for your Opinions"}
            </form>
            <br />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Contacts;
