import React from 'react'
import "./poltfolio.css"

function Poltfolio() {
  return (
    <div>
      <div className="poltfolio">
        <h3 className='poltfolio-title'>Poltfolio</h3>
        <div className="poltfolio-card">
          
          <div className="pcards">
            <h4>Online Learning System</h4>
            <p>An online learning system is a web-based platform that allows students and teachers to engage
in learning and teaching activities remotely.
Our system offers a wide range of courses in various fields.
You can take courses at your own pace, and learn from the comfort of your home.</p>
            <a href='https://pacifiquecyubahiro.github.io/online_learning_system.github.io/' target="_blank" rel="noreferrer">Learn more</a>
          </div>

          <div className="pcards">
            <h4>University Final Project (ULK)</h4>
            <p>An Online Employee Facial Recognition Attendance System is a technology that uses facial recognition technology to track employee attendance in a workplace. The system captures the employee's facial features and matches them to their identification in a database.</p>
            <a href='https://pacifiquecyubahiro.github.io/Year3csevFinalProject/' target="_blank" rel="noreferrer">Learn more</a>
          </div>

          <div className="pcards">
            <h4>RATI Rwanda</h4>
            <p>RATI is a specialized NGO that focuses on training Rwandan citizens
in technology to enhance their lives through ICT. Their main objective is to build ICT capacity among youth and individuals of all ages, aiming to improve their skills and knowledge in the field.</p>
            <a href='https://rati.rw/' target="_blank" rel="noreferrer">Learn more</a>
          </div>

        </div>
        <div className='btn'><button className='seemore'>See more</button></div>
      </div>
    </div>
  )
}

export default Poltfolio
